.advantages {
  padding-top: 90px;
  padding-bottom: 105px;
  background-image: url("../img/pale-mountains-desk.jpg");
  background-size: cover;
  background-position: center;
  background-color: $color-back-blue;

  @media (min-resolution: $retina-dppx), (min-resolution: $retina-dpi) {
    background: url("../img/pale-mountains-desk-2x.jpg") no-repeat;
  }
}

.advantages__wrap {
  width: calc(100% - 100px);
  max-width: 1266px;
  margin: 0 auto;
}

.advantages h2 {
  margin-top: 15px;
  margin-bottom: 50px;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
  text-align: center;
}

.advantages h3 {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
}

.advantages p {
  max-width: 288px;
  width: auto;
  margin-top: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
}

.advantages__list {
  justify-content: space-evenly;
  margin-top: 0;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  padding-left: 40px;
}

.advantages__item {
  width: calc(100% / 3 - 110px);
  max-width: 360px;
  position: relative;
  margin-right: 40px;
  margin-bottom: 10px;
  padding-left: 30px;
  padding-top: 63px;
  background-color: $color-font--main;
  padding-right: 30px;
  padding-bottom: 15px;
}

.advantages__item::before {
  content: "";
  position: absolute;
  background-size: cover;
  background-position: center;
  left: 30px;
}

.advantages__item--liner::before {
  width: 27px;
  height: 32px;
  top: 30px;
  background-image: url("../img/icon-liner.svg");
}

.advantages__item--allincluse::before {
  width: 38px;
  height: 21px;
  top: 32px;
  background-image: url("../img/icon-allinclusive.svg");
}

.advantages__item--zodiac::before {
  width: 42px;
  height: 15px;
  top: 37px;
  background-image: url("../img/icon-zodiac.svg");
}

@media (max-width: $tablet-width) {
  .advantages {
    padding-top: 80px;
    padding-bottom: 90px;
    background-image: url("../img/pale-mountains-tablet.jpg");
    background-size: cover;
    background-position: center;
    background-color: $color-back-blue;

    @media (min-resolution: $retina-dppx), (min-resolution: $retina-dpi) {
      background: url("../img/pale-mountains-tablet-2x.jpg") no-repeat;
    }
  }

  .advantages__wrap {
    width: 100%;
    max-width: 768px;
  }

  .advantages__list {
    justify-content: initial;
  }

  .advantages h2 {
    font-weight: 600;
    font-size: 26px;
    line-height: 150%;
  }

  .advantages h3:nth-child(2) {
    width: 133px;
  }

  .advantages__item {
    width: 190px;
    margin-right: 30px;
    padding: 0;
    padding-left: 20px;
    padding-top: 50px;
  }

  .advantages__item--liner::before {
    width: 21px;
    height: 26px;
    top: 30px;
    left: 20px;
    background-image: url("../img/icon-liner.svg");
  }

  .advantages__item--allincluse::before {
    width: 33px;
    height: 19px;
    top: 28px;
    left: 20px;
    background-image: url("../img/icon-allinclusive.svg");
  }

  .advantages__item--zodiac::before {
    width: 38px;
    height: 13px;
    top: 25px;
    left: 20px;
    background-image: url("../img/icon-zodiac.svg");
  }

  .advantages p {
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    padding-right: 17px;
  }
}

@media (max-width: $mobile-width) {
  .advantages {
    padding-top: 32px;
    padding-bottom: 15px;
    background-image: url("../img/pale-mountains-mobile.jpg");
    background-size: cover;
    background-position: center;
    background-color: $color-back-blue;

    @media (min-resolution: $retina-dppx), (min-resolution: $retina-dpi) {
      background: url("../img/pale-mountains-mobile-2x.jpg") no-repeat;
    }
  }

  .advantages__wrap {
    max-width: 290px;
  }

  .advantages__item {
    width: 255px;
    margin-bottom: 30px;
    padding: 0;
    padding-left: 20px;
    padding-right: 10px;
    padding-top: 54px;
    padding-bottom: 5px;
  }

  .advantages h3 {
    max-width: 170px;
    font-size: 18px;
  }

  .advantages h3:nth-child(2) {
    width: 170px;
  }

  .advantages h2 {
    margin-bottom: 30px;
    font-size: 24px;
  }

  .advantages__list {
    padding-left: 0;
    flex-direction: column;
  }

  .advantages p {
    padding: 0;
  }
}
